<template>
  <div class="w-full h-full">
    <div class="fixed top-[45px] left-[58px] flex text-white">
      <div class="cursor-pointer flex items-center" @click="back">
        <img src="~/images/back.png" alt="">
        <div class="ml-[15px] text-[24px]">
          返回
        </div>
      </div>
      <div class="cursor-pointer ml-[50px] flex items-center">
        <img src="~/images/share.png" alt="">
        <div class="ml-[15px] text-[24px]">
