        </div>
      </div>
    </div>
    <transition name="el-fade-in-linear" v-for="(item,i) in 4" :key="i">
      {{item}}
       <component :is="'detailTemplate'+(i+1)" @changeNav="changeNav" :current="i" v-show="current==i" />
      <!-- <detailTemplate1 @changeNav="changeNav" :current="current" v-show="!show"></detailTemplate1> -->
    </transition>

  </div>
</template>

<script>
import detailTemplate1 from "./components/detailTemplate1.vue"
import detailTemplate2 from "./components/detailTemplate2.vue"
import detailTemplate3 from "./components/detailTemplate3.vue"
import detailTemplate4 from "./components/detailTemplate4.vue"
export default {
  components: {
    detailTemplate1,
    detailTemplate2,
    detailTemplate3,
    detailTemplate4
  },
  name: "temp-detail",
  data(){
    return {
      show:false,
      current:0,
    }
  },
  methods: {
    back() {
      this.$router.go(-1)
    },
    changeNav(i){
      console.log('changeNav',i);
      this.current=i
    }
  }
}
</script>

