<template>
  <div class="flex">
    <div v-for="(item,i) in list" :key="i" class="w-[60px] h-[60px] rounded-full border-[3px]  hover:border-[#bfa584] mr-[14px] hover:cursor-pointer" :class="current==i?'active':''" :style="{
          background:`linear-gradient(rgba(0,0,0,${current==i?0:0.7}),rgba(0,0,0,${current==i?0:0.7})),url('${item.imageUrl}')`,backgroundSize: '100% 100%'}" @click="changeNav(i)">
      <!-- <img :src="item.imageUrl" alt=""  class="w-[60px] h-[60px] rounded-full border-[3px] border-[#bfa584]"> -->
    </div>
  </div>
</template>

<script>
export default {
  props: {
    current:{
      default:0
    },
    list: {
      default: [],
    }
  },
  methods:{
    changeNav(i){
      this.$emit("change",i)
    }
  }
}
</script>

<style scoped>

.active{
@apply border-[#bfa584]
}
</style>